import { useContext } from 'react';
import { Link } from 'react-router-dom';
import { MainContext } from '../../context/MainContext/MainContext';
import { TestContext } from '../../context/TestContext/TestContext';
import { Keyboard } from '../../generics/Keybord/Keyboard';
import { Crown } from '../../generics/Crown/Crown';
import { InfoIcon } from '../../generics/InfoIcon/InfoIcon';
import { Gear } from '../../generics/Gear/Gear';
import { UserIcon } from '../../generics/UserIcon/UserIcon';
import { Logout } from '../../generics/Logout/Logout';
import styles from './style.module.scss'

export const Header = ()=> {

    const { changeFinished, changeWordsList,wordsList: wordsData} = useContext(MainContext);

    const { 
        resetTestContext
    } = useContext(TestContext)

    const restartGame = () => {
        resetTestContext();
        changeWordsList([...wordsData]);
        changeFinished(true)
    }
    
    return (
        <header className={styles.Header}>
                <nav className={styles.nav}>
                    <div className={styles.leftNav}>
                        <Link to='/' className={styles.logo} onClick={restartGame}>
                            <h1>RStype</h1>
                        </Link>
                        <Link to='/' onClick={restartGame}>
                            <Keyboard />
                        </Link>
                        <Link to='/rating' onClick={restartGame}>
                            <Crown />
                        </Link>
                        <Link to='/info' onClick={restartGame}>
                            <InfoIcon />
                        </Link>
                        <Link to='/settings' onClick={restartGame}>
                            <Gear />
                        </Link>
                    </div>
                    <div className={styles.rightNav}>
                        <Link to='/user' onClick={restartGame}>
                            <UserIcon/>
                        </Link>
                        <Link to='/logout' onClick={restartGame}>
                            <Logout/>
                        </Link>
                    </div>
                </nav>
        </header>
    )
} 
        
    
